(function () {
  'use strict';

  angular.module('eventplanner', ['ui.router', 'ngMaterial', 'LocalForageModule']);
})();

(function () {
  'use strict';

  angular.module('eventplanner').config(routesConfig);

  function routesConfig($stateProvider) {
    "ngInject";

    $stateProvider
    .state('signup', {
      url: '/signup',
      templateUrl: 'app/signup/signup.html',
      controller: 'SignupController',
      controllerAs: 'vm'
    });
  }
})();

(function () {
  'use strict';

  angular.module('eventplanner').controller('SignupController', signupController);

  function signupController($localForage, $rootScope, $mdToast, $state) {
    "ngInject";

    var vm = this;

    vm.createUser = createUser;

    function createUser(form) {
      if (form.$valid) {
        $localForage.setItem('user', vm.user).then(function () {
          $localForage.getItem('user').then(function (data) {
            $rootScope.loggedUser = data;
            $mdToast.show(
              $mdToast.simple()
              .textContent('Welcome, ' + data.name)
              .hideDelay(3000)
            );
            $state.go('home');
          });
        });
      }
    }
  }
})();

(function () {
  'use strict';

  angular.module('eventplanner').controller('LogoutController', logoutController);

  function logoutController($rootScope, $mdToast, $state) {
    "ngInject";

    var vm = this;

    vm.logout = logout;

    function logout() {
      $mdToast.show(
        $mdToast.simple()
          .textContent('See you later! o/')
          .hideDelay(3000)
      );

      $rootScope.loggedUser = null;

      $state.go('home');
    }
  }
})();

(function () {
  'use strict';

  angular.module('eventplanner').config(routesConfig);

  function routesConfig($stateProvider) {
    "ngInject";

    $stateProvider
    .state('home', {
      url: '/',
      templateUrl: 'app/home/home.html',
      controller: 'HomeController',
      controllerAs: 'vm'
    });
  }
})();

(function () {
  'use strict';

  angular.module('eventplanner').controller('HomeController', homeController);

  function homeController($localForage, $rootScope, $mdToast, $log) {
    "ngInject";

    var vm = this;

    vm.login = login;

    function login(form) {
      if (form.$valid) {
        $localForage.getItem('user').then(function (data) {
          if (data) {
            if (data.email === vm.user.email && data.password === vm.user.password) {
              $rootScope.loggedUser = data;
              $mdToast.show(
                $mdToast.simple()
                .textContent('Welcome, ' + data.name)
                .hideDelay(3000)
              );
            } else {
              $mdToast.show(
                $mdToast.simple()
                .textContent('Invalid credentials!')
                .hideDelay(3000)
              );
            }
          } else {
            $mdToast.show(
              $mdToast.simple()
              .textContent('No registered users.')
              .hideDelay(3000)
            );
          }
        }).catch(function (error) {
          $mdToast.show(
            $mdToast.simple()
            .textContent('Unexpected error: ' + error)
            .hideDelay(3000)
          );
          $log.error(error);
        });
      }
    }
  }
})();

(function () {
  'use strict';

  angular.module('eventplanner').config(routesConfig);

  function routesConfig($stateProvider) {
    "ngInject";

    $stateProvider
    .state('event', {
      url: '/event',
      abstract: true,
      template: '<ui-view/>'
    })
    .state('event.list', {
      url: '/list',
      templateUrl: 'app/event/event.list.html',
      controller: 'EventListController',
      controllerAs: 'vm'
    })
    .state('event.detail', {
      url: '/view/:id',
      templateUrl: 'app/event/event.detail.html',
      controller: 'EventDetailController',
      controllerAs: 'vm'
    })
    .state('event.create', {
      url: '/create',
      templateUrl: 'app/event/event.form.html',
      controller: 'EventCreateController',
      controllerAs: 'vm'
    });
  }
})();

(function () {
  'use strict';

  angular.module('eventplanner').controller('EventListController', eventListController);

  function eventListController($localForage) {
    "ngInject";

    var vm = this;

    vm.init = init;

    vm.init();

    function init() {
      $localForage.getItem('events').then(function (data) {
        vm.events = data;
      });
    }
  }
})();

(function () {
  'use strict';

  angular.module('eventplanner').controller('EventDetailController', eventDetailController);

  function eventDetailController($mdToast, $log, $localForage, $stateParams) {
    "ngInject";

    var vm = this;

    vm.init = init;

    vm.init();

    function init() {
      loadEvent();
    }

    function loadEvent() {
      $localForage.getItem('events').then(function (events) {
        vm.event = events.filter(function (event) {
          return (event.id == $stateParams.id);
        })[0];
      }).catch(function (error) {
        $mdToast.show(
          $mdToast.simple()
          .textContent('Unexpected error: ' + error)
          .hideDelay(3000)
        );
        $log.error(error);
      });
    }
  }
})();

(function () {
  'use strict';

  angular.module('eventplanner').controller('EventCreateController', eventCreateController);

  function eventCreateController($mdConstant, $localForage, $rootScope, $mdToast, $state) {
    "ngInject";

    var vm = this;

    vm.event = {
      guests: []
    };

    vm.eventTypeList = [
      {value: 'birthday', label: 'Birthday'},
      {value: 'conferenceTalk', label: 'Conference Talk'},
      {value: 'party', label: 'Party'},
      {value: 'show', label: 'Show'},
      {value: 'wedding', label: 'Wedding'}
    ];

    vm.guests = [];

    vm.states = [
      {abbrev: 'BA', name: 'Bahia'},
      {abbrev: 'MG', name: 'Minas Gerais'},
      {abbrev: 'RJ', name: 'Rio de Janeiro'},
      {abbrev: 'SP', name: 'SÃ£o Paulo'}
    ];

    vm.separatorKeys = [$mdConstant.KEY_CODE.ENTER, $mdConstant.KEY_CODE.COMMA, /* semicolon = 186 */ 186];

    vm.createEvent = createEvent;

    function createEvent(form) {
      //FIXME issue with localforage and date

      if (form.$valid) {
        $localForage.getItem('events').then(function (data) {
          var events = data || [];

          vm.event.id = Date.now();

          events.push(vm.event);

          $localForage.setItem('events', events).then(function () {
            $mdToast.show(
              $mdToast.simple()
              .textContent(vm.event.name + ' created!')
              .hideDelay(3000)
            );
            $state.go('event.list');
          });
        });
      }
    }
  }
})();

(function () {
  'use strict';

  angular.module('eventplanner').config(routesConfig);

  function routesConfig($stateProvider, $urlRouterProvider, $locationProvider) {
    "ngInject";

    $locationProvider.html5Mode(true).hashPrefix('!');
    $urlRouterProvider.otherwise('/');

    $stateProvider
    .state('app', {
      url: '/',
      component: 'app'
    });
  }
})();

(function () {
  'use strict';

  angular.module('eventplanner').config(appConfig);

  function appConfig() {
    "ngInject";
  }
})();

